body {
  direction: rtl;
  text-align: right;
  background-color: #f1f1f1;
  font-family: "Tajawal", sans-serif;
}

h2 {
  color: $brandColor;
  background-color: transparent;
  padding: 10px;
  font-size: 30px;
  margin: 0 auto 30px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: 0px;
    right: 10px;
    width: 50px;
    height: 3px;
    transition: .3s;
    background-color: $mainColor
  }
  &::before {
    content: "";
    position: absolute;
    bottom: -10px;
    right: 10px;
    width: 30px;
    height: 3px;
    transition: .3s;
    background-color: $mainColor
  }
}

p {
  font-size: 20px;
}
