/* Footer */
footer {
    padding: 50px 0;
    background-color: $brandColor;
    .container {
        margin: 0 auto;
        padding: 0;
        box-shadow: none;
        width: 100% !important;
        background-color: transparent
    }
    .scoail {
        text-align: center;
        .btn {
            &:focus {
                outline: 0;
                box-shadow: none
            }
            i {
                font-size: 20px;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                color: #fff;
                background-color: #0e1016;
                box-shadow: 0 0 3px 0 $brandColor;
                transition: .3s ease;
            }
            &:nth-child(1):hover {
                i {
                    background-color: #3b5999
                }
            }
            &:nth-child(2):hover {
                i {
                    background-color: #55acee
                }
            }
            &:nth-child(3):hover {
                i {
                    background-color: #e4405f
                }
            }
            &:nth-child(4):hover {
                i {
                    background-color: #0077B5
                }
            }
            &:nth-child(5):hover {
                i {
                    background-color: $mainColor
                }
            }
        }
    }
}