.container {
    background-color: #fff;
    margin-top: -80px;
    margin-bottom: 100px;
    padding: 30px 50px;
    box-shadow: 0 0 2px 0 rgba($brandColor, .3);
}

/* About Us */
.about-us {
    padding: 50px 0;
    h3 {
        font-size: 17px;
        padding: 8px;
        font-weight: 600;
        margin: 30px 0 20px;
        width: 200px;
        color: #fff;
        border-right: 4px solid $brandColor;
        text-shadow: 0 0 3px rgba($brandColor, .3);
        background-color: $mainColor
    }
    p {
        font-size: 18px;
        span {
            display: inline-block;
            font-size: 16px;
            padding: 5px;
            width: 45%;
            background-color: #f1f1f1;
            margin: 5px;
            box-shadow: 0 0 1px 0 rgba($brandColor, .3);
            i {
                font-size: 14px;
                margin: 0 5px;
                color: $mainColor
            }
        }
    }
}

/* Our Services */
.our-services {
    padding: 50px 0;
    .info {
        justify-content: center;
        .text {
            height: 150px;
            background-color: #f1f1f1;
            box-shadow: 0 0 2px 0 rgba($brandColor, .3);
            margin: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            position: relative;
            p {
                opacity: 0;
                color: #333;
                transition: .3s
            }
            span {
                position: absolute;
                display: block;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: $mainColor;
                font-size: 45px !important;
                text-align: center;
                width: 100%;
                transition: .3s;
            }
            &:hover {
                span {
                    opacity: 0;
                }
                p {
                    opacity: 1;
                }
            }
        }
    }
}

/* Our Products */
.our-product {
    padding: 50px 0;
    .content {
        width: 100%;
        height: 230px;
        background-color: #f1f1f1;
        border-radius: 5px;
        box-shadow: 0 0 3px 0 rgba($brandColor, .3);
        overflow: hidden;
        position: relative;
        margin-bottom: 20px;
        transition: .2s ease;
        .img {
            width: 95%;
            height: 210px;
            margin: 10px auto;
            border-radius: 5px;
            position: relative;
            transition: .3s ease;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba($brandColor, .3)
            }
            img {
                border-radius: 5px;
            }
        }
        .overflow {
            font-size: 14px;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(to top, rgba($brandColor, .8), transparent);
            transform: translateY(60px);
            opacity: 0;
            width: 100%;
            height: 100%;
            transition: .3s ease;
            p {
                color: #fff;
                padding: 15px;
                font-size: 18px;
                margin: 40% 0
            }
        }
        &:hover {
            .overflow {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}